<template>
  <div class="video-player-box">
    <h3 class="video-player-h3"><i class="el-icon-circle-close video-player-icon" @click="videoClose"></i></h3>
    <!-- <div class="player"> -->
    <video-player class="video-player vjs-custom-skin" ref="videoPlayer" :playsinline="true" :options="playerOptions" @play="onPlayerPlay($event)" @pause="onPlayerPause($event)">
    </video-player>
    <!-- </div> -->
  </div>
</template>

<script>
/*
 * 
 * auth:weilan
 * time:2018-10-16
 */
require("video.js/dist/video-js.css");
require("vue-video-player/src/custom-theme.css");
import { videoPlayer } from "vue-video-player";

export default {
  data() {
    return {
      selfOptions: {
        playbackRates: [0.7, 1.0, 1.5, 2.0], //
        autoplay: true, //true,
        muted: false, // 
        loop: false, // 
        preload: "auto", // <video>auto,
        language: "zh-CN",
        aspectRatio: "16:9", //  - "16:9""4:3"
        fluid: true, // trueVideo.js player
        sources: [
          {
            type: "video/mp4",
            src: "" //
          }
        ],
        poster: "", //
        width: document.documentElement.clientWidth,
        notSupportedMessage: "" //Video.js
        //        controlBar: {
        //          timeDivider: true,
        //          durationDisplay: true,
        //          remainingTimeDisplay: false,
        //          fullscreenToggle: true  //
        //        }
      }
    };
  },
  props: {
    options: Object
  },
  methods: {
    videoClose() {
      this.$emit("closeVideo");
    }
  },
  computed: {
    playerOptions() {
      return { ...this.selfOptions, ...this.options };
    },
    player() {
      return this.$refs.videoPlayer.player;
    }
  },
  components: { videoPlayer }
};
</script>

<style lang="scss">
.video-player-box {
  position: absolute;
  top: 10%;
  left: 10%;
  z-index: 99;
  width: 80%;
  // height: 80%;
  background-color: #efefef;
  /*  > .player {
    position: relative;
  } */
  > .video-player-h3 {
    position: relative;
    z-index: 9;
    margin-bottom: -10px;
    padding: 10px 15px 0;
    height: 26px;
    text-align: right;
    background-color: #000;

    > .video-player-icon {
      color: #fff;
      font-size: 20px;
      cursor: pointer;
    }
  }
}
</style>
